(() => {
  const product = document.querySelector(`.products__slider`);
  const buttonNextText = document.querySelector(`.products__button--next span`);
  const buttonPrevText = document.querySelector(`.products__button--prev span`);

  const productSlider = new window.Swiper(product, {
    slidesPerView: 1,
    spaceBetween: 20,
    a11y: false,
    simulateTouch: true,
    loop: true,
    autoHeight: true,
    watchOverflow: true,
    navigation: {
      nextEl: `.products__button--next`,
      prevEl: `.products__button--prev`,
    },
    pagination: {
      el: `.products__dots`,
      type: `bullets`,
      clickable: true
    },
    breakpoints: {
      921: {
        simulateTouch: false,
      }
    }
  });

  buttonNextText.textContent = document.querySelector(`.swiper-slide-next h3`).textContent;
  buttonPrevText.textContent = document.querySelector(`.swiper-slide-prev h3`).textContent;

  function getButtonsText(e) {
    const slideActive = document.querySelector(`.swiper-slide-active`);
    const titleNext = document.querySelector(`.swiper-slide-next h3`);
    const titlePrev = document.querySelector(`.swiper-slide-prev h3`);
    const slides = document.querySelectorAll(`.products__item`);
    const slidesLength = String(slides.length - 3);

    for (let slide of slides) {
      if (slideActive.classList.contains(`swiper-slide-duplicate`)) {
        if (e.target.classList.contains(`products__button--next`)) {
          if (slide.getAttribute(`data-swiper-slide-index`) === `1`) {
            buttonNextText.textContent = slide.querySelector(`h3`).textContent;
          }

          if (slide.getAttribute(`data-swiper-slide-index`) === slidesLength) {
            buttonPrevText.textContent = slide.querySelector(`h3`).textContent;
          }
        }

        if (e.target.classList.contains(`products__button--prev`)) {
          if (slide.getAttribute(`data-swiper-slide-index`) === `0`) {
            buttonNextText.textContent = slide.querySelector(`h3`).textContent;
          }

          if (slide.getAttribute(`data-swiper-slide-index`) === String(slidesLength - 1)) {
            buttonPrevText.textContent = slide.querySelector(`h3`).textContent;
          }
        }
      } else {
        buttonNextText.textContent = titleNext.textContent;
        buttonPrevText.textContent = titlePrev.textContent;
      }
    }
  }

  document.addEventListener(`click`, (e) => {
    if (e.target.classList.contains(`products__button--next`)
      || e.target.classList.contains(`products__button--prev`)
      || e.target.classList.contains(`products__button-title`)) {
      getButtonsText(e);
    }
  });
})();
